export const mixin = {
    methods:{
        //
        notify(title,type){
            this.$notify({
                title: title,
                type: type
            })
        },
        //
        getUrl(url){
            return `${this.$store.state.HOST}/${url}`
        },
        //
        changeSex(value){
            if(value == 0){
                return '';
            }
            if(value == 1){
                return '';
            }
            if(value == 2){
                return '';
            }
            if(value == 3){
                return '';
            }
            return value;
        },
        //
        attachBirth(val){
            return String(val).substr(0,10);
        },
        //
        beforeAvatarUpload(file){
            const isJPG = (file.type === 'image/jpeg')||(file.type === 'image/png')||(file.type === 'image/jpg');
            if(!isJPG){
                this.$message.error('');
                return false;
            }
            const isLt2M = (file.size / 1024 /1024) < 10;
            if(!isLt2M){
                this.$message.error('');
                return false;
            }
            return true;
        },
        //
        handleAvatarSuccess(res){
            let _this = this;
            if(res.code == 1){
                _this.getData();
                _this.$notify({
                    title: '',
                    type: 'success'
                });
            }else{
                _this.$notify({
                    title: '',
                    type: 'error'
                });
            }
        },
        //
        handleDelete(id){
            this.idx = id;
            this.delVisible = true;
        },
        //multipleSelection
        handleSelectionChange(val){
            this.multipleSelection = val;
        },
        //
        delBatch(){
            for(let item of this.multipleSelection){
                this.handleDelete(item.id);
                this.deleteRow();
            }
            this.multipleSelection = [];
        }
    }
}


